'use strict';

function generate(start_color, end_color) {
  var midpoint = arguments.length > 2 && arguments[2] !== undefined ? arguments[2] : 1;
  midpoint = midpoint < 0 ? 0 : midpoint;
  midpoint++;
  var start = hexToRGB(start_color);
  var end = hexToRGB(end_color);
  var result = [];
  var average = end.map(function (val, i) {
    return (val - start[i]) / midpoint;
  });

  var _loop = function _loop(i) {
    var temp = start.map(function (val, k) {
      return Math.round(val + average[k] * i);
    });
    result.push("rgb(" + temp.join(',') + ")");
  };

  for (var i = 0; i <= midpoint; i++) {
    _loop(i);
  }

  return result;
}

function hexToRGB(hex) {
  return (hex = hex.replace('#', '')).match(new RegExp('(.{' + hex.length / 3 + '})', 'g')).map(function (l) {
    return parseInt(hex.length % 2 ? l + l : l, 16);
  });
}

module.exports = generate;